.Department {
	> .inner {
		max-width: 100%;
		padding: 0;
		padding-top: 170px;
		.con {
			display: flex;
			justify-content: flex-end;
			max-width: 1280px;
			width: 100%;
			margin: 0 auto;
			margin-bottom: 150px;
			article {
				position: relative;
				width: 80%;
				display: flex;
				justify-content: flex-end;
				.pic {
					position: absolute;
					top: 15%;
					left: 0;
					width: 230px;
					height: 70%;
					z-index: 2;
					box-shadow: 10px 10px 15px rgb(0 0 0 / 30%);
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
				.box {
					position: relative;
					width: 82%;
					padding: 100px;
					background: #f5f5f5;
					overflow: hidden;
					&::after {
						content: 'arch';
						display: block;
						position: absolute;
						bottom: 0;
						right: 0;
						font-size: 20em;
						font-weight: 700;
						color: #fff;
						line-height: 0.7;
						transform: rotate(-90deg) translate(40%, 115%);
					}
					.text {
						width: 50%;
						margin-left: 7%;
						h2 {
							font: 500 22px/1 'Oswald';
							margin-bottom: 20px;
						}
						p {
							font-size: 14px;
							line-height: 1.4;
							margin-bottom: 25px;
						}
						b {
							font-size: 14px;
							line-height: 2;

							&::before {
								content: '';
								display: inline-block;
								width: 5px;
								height: 5px;
								vertical-align: middle;
								margin-right: 5px;
								background: #c6a47e;
							}
						}
					}
				}
			}
		}

		.wrap {
			background: #f5f5f5;
			padding: 100px 0 150px;
			> .inner {
				display: flex;
				flex-wrap: wrap;
				max-width: 1280px;
				width: 100%;
				margin: 0 auto;

				.l_box {
					width: 27%;
					.text {
						margin-right: 50px;
						padding: 50px 30px;
						background: #c6a47e;
						span {
							font-size: 12px;
							color: #fff;
						}
						p {
							font-weight: 500;
							color: #fff;
							line-height: 1.5;
							margin-top: 15px;
							margin-bottom: 60px;
						}
					}
				}
				.list {
					width: 73%;
					display: flex;
					flex-wrap: wrap;
					article {
						width: 25%;
						padding: 0 20px;
						margin-bottom: 50px;
						.inner {
							position: relative;
							width: 100%;
							box-shadow: 5px 8px 10px rgba(0, 0, 0, 0.3);
							.pic {
								width: 100%;
								height: 280px;
								img {
									width: 100%;
									height: 100%;
									object-position: center top;
									object-fit: cover;
									filter: grayscale(0.5);
								}
							}
							.txt {
								position: absolute;
								left: 0;
								bottom: 0;
								width: 100%;
								background: #fff;
								text-align: center;
								padding: 10px 0;
								h2 {
									font-size: 14px;
									margin-bottom: 5px;
								}
								p {
									font-size: 12px;
								}
							}
						}
					}
				}
			}
		}
	}
}

@media screen and (max-width: $web_s) {
	.Department {
		> .inner {
			.con {
				padding: 0 5vw;
				article {
					width: 95%;
				}
			}

			.wrap {
				> .inner {
					padding: 0 5vw;
					.l_box {
						.text {
							margin-right: 30px;
						}
					}
					.list {
						article {
							width: calc(100% / 3);
							.inner {
								.pic {
									height: 300px;
								}
							}
						}
					}
				}
			}
		}
	}
}
